import React, { useEffect, useState } from 'react';
import Layout from '../../containers/layout'
import {
    Anchor,
    Box,
    Card,
    CardBody,
    CardFooter,
    Heading,
    Image,
  } from 'grommet';
import { graphql } from 'gatsby'

var shuffle = function (array) {

	var currentIndex = array.length;
	var temporaryValue, randomIndex;

	// While there remain elements to shuffle...
	while (0 !== currentIndex) {
		// Pick a remaining element...
		randomIndex = Math.floor(Math.random() * currentIndex);
		currentIndex -= 1;

		// And swap it with the current element.
		temporaryValue = array[currentIndex];
		array[currentIndex] = array[randomIndex];
		array[randomIndex] = temporaryValue;
	}

	return array;

};
export const query = graphql`
    query HomePage {


    allYoutubeVideo {
        edges {
            node {
                id
                title
                description
                videoId
                publishedAt
                privacyStatus
                channelTitle
                thumbnail {
                    url
                }
            }
        }
    }
}
`

const Index = ({data}) => {
    const [dataList, setDataList] = useState(data.allYoutubeVideo.edges)

    useEffect(() => {
      setDataList( shuffle(dataList.slice()) )
    }, [])

    return (
        <Layout max="1240px">
            <p><Anchor href="mailto:openfengshui@gmail.com" label="Contact" /> us to add/remove your youtube channel </p>
            <Box
                direction="row"
                wrap={true}
                justify="center"
            >
              
            {
                dataList
                .map((e, i) => {
                
                const { thumbnail, title, description, videoId } = e.node;
                return  <Box
                    key={i}
                    pad="small" 
                ><Card elevation="large" width="small">
                  <CardBody height="small">
                    <Image
                      fit="cover"
                      src={thumbnail.url}
                      a11yTitle="bridge"
                    />
                  </CardBody>
                  <Box pad={{ horizontal: 'medium' }} responsive={false}>
                    <Heading level="6" margin={{ vertical: 'medium' }}>
                      { title }
                    </Heading>
                  </Box>
                  <CardFooter>
                    <Box fill={true} justify="center" direction="row" align="center" gap="small">
                    <Anchor href={`http://www.youtube.com/watch?v=${videoId}`} label="Watch" />
                    </Box>

                  </CardFooter>

                </Card>
                </Box>
                })
            }
            </Box>
        </Layout>
    );
}

export default Index;